import React, {useState, useEffect} from "react";
import { Nav } from "./home";

const Challenge = ({ name, hard, passed, id, loved }) => {
    var points = 0;
    if (hard === "very-hard") {
        points = 25;
    } else if (hard === "hard") {
        points = 20;
    }   else if (hard === "normal") {
        points = 15;
    }   else if (hard === "easy") {
        points = 10;
    }
    return <>
        <div className={`challenge ${hard}-challenge col-12 d-flex p-3 my-2 d-flex justify-content-between align-items-center`} onClick={() => {
            window.location.pathname = `/challenge/${id}`;
        }}>
            <h5 className="p-0 m-0">{name}</h5>
            <div className="d-flex align-items-center">
                <span className="flag mx-2">
                    {points} 
                </span>
                <span className={`${passed ? "signin" : "signin bg-secondary"} mx-2 text-dark fw-bold w-90px py-1 text-center`}>
                    {passed ? " ": " "}
                </span>
            </div>
        </div>
    </>
}

const Challenges = () => {
    const [d, setD] = useState([]);
    const [type, setType] = useState("easy");
    var from = 0;

    useEffect(() => {
        fetch(`/challenges?skip=0&hardness=easy`)
            .then(res => res.json())
            .then(data => {
                setD(data);
            })
            .catch(err => console.log(err));
    }, []);
    
    const set = (hard) => {
        fetch(`/challenges?skip=0&hardness=${hard}`)
            .then(res => res.json())
            .then(data => setD(data))
            .catch(err => console.log(err));
        setType(hard);
    }

    const load = () => {
        if (d[d.length - 1].next) {
            from = from + 20;
            fetch(`/challenges?skip=${from}&hardness=${type}`)
            .then(res => res.json())
            .then(data => setD([...d, data[0]]))
            .catch(err => console.log(err));
        }
    }

    const check = (challengeName) => {
        let userData = JSON.parse(localStorage.getItem("data"));

        if (userData) {
            if (userData.passedChallenges.indexOf(challengeName) === -1) {
                return false
            }else {
                return true;
            }
        }else {
            return false;
        }
    }

    return <>
        <Nav/>
        <section className="container top">
            <div className="d-flex justify-content-center my-3">
                <button className="btn-2 mx-3 px-2 py-1 fs-6 easy" disabled={type === "easy" ? true:false} onClick={() => set("easy")}></button>
                <button className="btn-2 mx-3 px-2 py-1 fs-6 normal" disabled={type === "normal" ? true:false} onClick={() => set("normal")}></button>
                <button className="btn-2 mx-3 px-2 py-1 fs-6 hard" disabled={type === "hard" ? true:false} onClick={() => set("hard")}></button>
                <button className="btn-2 mx-3 px-2 py-1 fs-6 very-hard" disabled={type === "very-hard" ? true:false} onClick={() => set("very-hard")}> </button>
            </div>
            <div className="row px-md-0 px-3">
                {d.map((obj, index) => {
                    return <Challenge name={obj.name} key={index} id={obj._id} hard={obj.type} passed={check(obj.name)}/>
                })}
            </div>
            <div className="w-100 d-flex justify-content-center">
                <button className="signin px-2 py-1 fw-bold" id="load" onClick={() => load()}></button>
            </div>
        </section>
    </>
}

export { Challenges };